import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { getData, postData } from "../../../config/axios.jsx";
import HeadFormHakAkses from "./HeadFormHakAkses";
class FormHakAkses extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      selected: [],
      nama: "samsul"
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.selectedAkses = this.selectedAkses.bind(this);
  }

  handleSubmit(input) {
    this.setState({
      isLoading : true
    })
    var data = this.state.selected || [];
    var daftarArray = [];
    data.forEach((data, index) => {
      var hasil = {
        user_id: input.user_id,
        menu_id: data,
        menu_name: data,
      };
      daftarArray.push(hasil);
    });

    console.log(daftarArray);
    postData('akses-user/add', daftarArray).then((res) => {
      ToastNotification("success", 'Hak Akses Berhasil Disimpan');
    }).then(() => {
      this.setState({ selected : [],isLoading:false });
      this.props.dispatch(reset('HeadFormHakAkses'))
    }).catch((err) => {
      this.setState({
        isLoading:false
      })
      ToastNotification("info", 'Terjadi Kesalahan Saat Mengirim Data');
    })
  }
  selectedAkses = (selected) => {
    this.setState({ selected });
  };

  getDataUsers(e) {
    getData('sales/get/by-kode-sales/' + e).then((res) => {
      // console.log(res.data.nama_sales);
      document.getElementById('nama_user').value = res.data.nama_sales === undefined ? e : res.data.nama_sales
      // this.props.change('nama_user',res.data.nama_sales)
      // this.props.change('user_id',res.data.kode_sales)
    })
    let selected = [];
    getData('akses-user/id/' + e).then((res) => {
      res.data.forEach(element => {
        selected.push(element.menu_name)
      });
      this.setState({ selected })
    }).catch((err) => {
      console.log(err);
    })
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Manage Users</Link>
          </li>
          <li className="breadcrumb-item active">Hak Akses</li>
        </ol>
        <h1 className="page-header">Hak Akses </h1>
        <Panel>
          <PanelHeader>Hak Akses</PanelHeader>
          <br />
          {/* Hak Akses */}
          <div className="container">
            <HeadFormHakAkses
              selected={this.state.selected}
              isLoading={this.state.isLoading}
              getDataUsers={(data) => this.getDataUsers(data)}
              selectedAkses={(data) => this.selectedAkses(data)}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
          {/* End Tambah Hak Akses  */}
        </Panel>
      </div>
    );
  }
}

FormHakAkses = reduxForm({
  form: "FormHakAkses",
  enableReinitialize: true,
})(FormHakAkses);
export default connect()(FormHakAkses);
